import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { StaffService } from '../core/staff.service';
import { VendorService } from '../core/vendor.service';

@Injectable()
export class StaffManagerService {

  private _staffList$ = new BehaviorSubject<any[]>(null);
  get staffList$(): Observable<any[]> {
    return this._staffList$.asObservable();
  }


    private _vendorList$ = new BehaviorSubject<any[]>(null);
  get vendorList$(): Observable<any[]> {
    return this._vendorList$.asObservable();
  }

  constructor(private staffService: StaffService, private vendorService: VendorService) { 
    this.getVendorList();

  }

  createStaff(user: any): Observable<any> {
    const staff = {
      name: user['name'],
      email: user['email'],
      password: user['password'],
      city: user['city'],
      mobile: user['mobile'],
      status: user['status']
    };
    return this.staffService.createStaff(staff);
  }

  updateStaff(user: any): Observable<any> {
    const staff = {
      _id: user['_id'],
      name: user['name'],
      email: user['email'],
      city: user['city'],
      mobile: user['mobile'],
      status: user['status']
    };
    return this.staffService.updateStaff(staff);
  }

  deleteStaff(id: string): Observable<any> {
    return this.staffService.deleteStaff(id);
  }

  getStaff(id: string): Observable<any> {
    return this.staffService.getStaff(id);
  }


  getStaffList(): void {
    this.staffService.getStaffList()
      .subscribe((data: any[]) => {
        console.log('Got Staffs for you: ', data);

        this._staffList$.next(data);
      });
  }
  
  getVendorList(): void {
    this.vendorService.getVendorList()
      .subscribe((data: any[]) => {
        console.log('Got Vendors for you: ', data);

        this._vendorList$.next(data);
      });
  }
}
