import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { StaffManagerService } from '../staff-manager.service';
import { empty } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable()
export class StaffDetailResolver implements Resolve<any> {
  constructor(private staffManagerService: StaffManagerService) { }

  resolve(route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    console.log('router', route.params);
    return this.staffManagerService.getStaff(route.params['id']).pipe(
      catchError((error) => {
        return empty();
      })
    );
  }
}
